import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { AmbienteTrabalho, CLASS_NAME } from '../../../../models/dtos/mfp/ambienteTrabalho';
import { LOCAL_AMBIENTE_TRABALHO_ENUM_VALUES } from '../../../../models/enumerated/ger/localAmbienteTrabalhoEnum';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const AmbienteTrabalhoSearchView: FC<SearchViewProps<AmbienteTrabalho>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    enumColumn(
      {
        dataField: 'localAmbienteTrabalho',
        filter: true,
        sort: true,
        text: 'Ambiente de Trabalho',
      },
      LOCAL_AMBIENTE_TRABALHO_ENUM_VALUES
    ),
  ];
  return (
    <WindowComponent title="Manutenção de Ambiente de Trabalho">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'AmbienteTrabalhoEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default AmbienteTrabalhoSearchView;
